<!--  -->
<template>
  <div class="warp">
    <el-form :model="formData" label-width="80px">
      <el-form-item label="">
        <el-input v-model="formData.shopName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.shopTitle" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.shopDescribe" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formData.shopClassify" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            type="fixed-time"
            placeholder=""
            v-model="form.date2"
            style="width: 100%;"
          ></el-time-picker>
        </el-col>
      </el-form-item>-->
      <el-form-item label="">
        <el-switch v-model="formData.shopPutaway"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="formData.shopProperty">
          <el-checkbox label="" name="shopProperty"></el-checkbox>
          <el-checkbox label="" name="shopProperty"></el-checkbox>
          <el-checkbox label="" name="shopProperty"></el-checkbox>
          <el-checkbox label="" name="shopProperty"></el-checkbox>
          <el-checkbox label="" name="shopProperty"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <!-- <el-form-item label="">
        <el-radio-group v-model="form.resource">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>-->
      <el-form-item label="">
        <div class="Price">
          <el-input v-model="formData.shopPrice.old" placeholder=""></el-input>
          <el-input v-model="formData.shopPrice.new" placeholder=""></el-input>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData.shopInventory" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <div class="upload" @click="uploadImg">
          <i class="el-icon-upload" v-if="isShow"></i>
          <img :src="avatar" v-else>
          <input
            type="file"
            accept="image/*"
            capture="camcorder"
            @change="handleFile"
            class="hiddenInput"
            style="display: none;"
          >
        </div>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="addShop"></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        shopName: "", //
        shopTitle: "", //
        shopPrice: {
          old: "", //
          new: "" //
        }, //
        shopClassify: "", //
        shopDescribe: "", //
        shopInventory: 0, //
        shopPutaway: false, //
        shopProperty: [] //
      },
      postData: "",
      isShow: true,
      avatar: "",
      options: [
        {
          label: "",
          value: 1
        },
        {
          label: "",
          value: 2
        },
        {
          label: "",
          value: 3
        },
        {
          label: "",
          value: 4
        },
        {
          label: "",
          value: 5
        },
        {
          label: "",
          value: 6
        },
        {
          label: "",
          value: 7
        }
      ]
    };
  },

  components: {},

  computed: {},

  mounted: function() {},

  methods: {
    uploadImg() {
      // class=hiddenInput 
      this.$el.querySelector(".hiddenInput").click();
    },
    handleFile(e) {
      // console.log(e);
      let file = e.target.files[0];
      let form = new FormData();
      form.append("myfile", file);
      form.set("shopName", this.formData.shopName);
      form.set("shopTitle", this.formData.shopTitle);
      form.set("shopOldPrice", this.formData.shopPrice.old);
      form.set("shopNewPrice", this.formData.shopPrice.new);
      form.set("shopClassify", this.formData.shopClassify);
      form.set("shopDescribe", this.formData.shopDescribe);
      form.set("shopPutaway", this.formData.shopPutaway);
      form.set("shopProperty", this.formData.shopProperty);
      form.set("shopInventory", this.formData.shopInventory);
      this.postData = form;
      var reader = new FileReader();
      // 
      reader.onload = data => {
        let res = data.target || data.srcElement;
        this.avatar = res.result;
        this.isShow = false;
      };
      reader.readAsDataURL(file);
    },
    addShop() {
      //
      if (this.avatar == "") {
        this.$message({
          message: "",
          type: "error"
        });
      } else {
        this.$getData("/shop/add", this.postData, "post", res => {
          console.log(res);
          if (res.data.code == 1) {
            this.$message({
              message: res.data.msg,
              type: "success"
            });
            this.$router.push("shoplist");
          } else {
            this.$message({
              message: res.data.msg,
              type: "error"
            });
          }
        });
      }
    }
  }
};
</script>
<style scoped>
.warp {
  margin-left: 250px;
  margin-top: 50px;
  width: 50%;
}
.Price {
  display: flex;
  flex-direction: row;
}
.upload {
  border: 1px solid gray;
  width: 80px;
  height: 80px;
  margin-left: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.upload > i {
  font-size: 30px;
  margin: 0 auto;
  color: grey;
}
.upload > input {
  display: none;
}
.upload > img {
  width: 80px;
  height: 80px;
}
</style>


